
body {
  background-color: #222;
  color: white;
  text-align: center;
}

b {
  color: #48b4fc;
}

nav {
  background-color: #000;
  text-align: left;
  padding: 12px;
}


span {
  margin: 16px;
  border-radius: 6%;
  padding: 4px;
}

a {
  color: #fcb4a8;
}

table {
  text-align: center;
  margin: auto;
  width: auto;
}

th, button {
  font-weight: bold;
  text-align: left;
  color: #222;
  background-color: #2424fc;
  box-shadow: 2px 4px 2px 1px rgba(0, 0, 0, .2);
  border-radius: 6%;
  padding: 4px;
}

tr {
  font-size: large;  
}

td {
  background-color: #fffff0;
  border-style: none;
  border-radius: 6%;
  box-shadow: 2px 4px 2px 1px rgba(0, 0, 0, .2);
}

.content {
  width: 35%;
  margin: auto;
}

.input {
  text-align: right;
}